"use client";

import { useSearchParams } from "next/navigation";
import { Card } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";

export default function ResultPage() {
  const searchParams = useSearchParams();
  const verdict = searchParams.get("verdict") || "unknown";
  const confidence = Number(searchParams.get("confidence")) || 0;
  const signals = searchParams.get("signals")?.split(",") || [];

  const color =
    verdict === "safe" ? "bg-green-200" :
    verdict === "phishing" ? "bg-red-200" :
    "bg-yellow-200";

  return (
    <div className="flex flex-col items-center justify-center min-h-screen p-6 bg-gray-50">
      <h1 className="text-3xl font-bold mb-6">Detection Result</h1>
      <Card className={`p-6 w-full max-w-lg ${color}`}>
        <h2 className="text-2xl font-semibold mb-2 capitalize">{verdict}</h2>
        <p className="mb-4">Confidence: {confidence}%</p>
        <Progress value={confidence} className="mb-4" />
        {signals.length > 0 && (
          <>
            <h3 className="font-semibold mb-2">Triggered Signals:</h3>
            <ul className="list-disc list-inside">
              {signals.map((s, i) => (
                <li key={i}>{s}</li>
              ))}
            </ul>
          </>
        )}
      </Card>
    </div>
  );
}
